<template>
  <header :class="{login: isLogin, 'no-login': !isLogin}">
    <template v-if="!isLogin">
      <h1>Share</h1>
      <p>精品博客汇聚</p>
      <div class="btns">
        <router-link to="/login"><el-button>立即登陆</el-button></router-link>
        <router-link to="/register"><el-button>注册账号</el-button></router-link>
      </div>
    </template>

    <template v-if="isLogin">
      <h1><router-link to="/">Share</router-link></h1>
      <router-link to="/create"><i class="edit el-icon-plus"></i></router-link>
      <div class="user">
        <img class="avatar" :src="user.avatar" :alt="user.username" :title="user.username">
        <ul>
          <li><router-link to="/my">我的</router-link></li>
          <li><a href="" @click="onLogout">注销</a></li>
        </ul>
      </div>
    </template>
  </header>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
    data(){
      return {
      }
    },
    computed:{
      ...mapGetters([
        'isLogin',
        'user'
      ])
    },
    created(){
      this.checkLogin()
    },
    methods:{
      ...mapActions([
        'checkLogin',
        'logout'
      ]),
      onLogout(){
        this.logout()
      }

    }
  }
</script>

<style lang="less">
@import "../assets/base";
  header.no-login{
    padding: 0.1px 12% 30px 12%;
    background: @bgcolor; //引用@import里的样式
    display: grid;
    justify-items: center;
    text-align: center;
    h1{
      color: #fff;
      font-size: 40px;
      margin: 60px 0 0 0;
      a{
        color: #fff;
      }
    }
    p{
      margin: 15px 0 0 0;
      color: #fff;
    }
    button{
      margin: 20px 5px 0;
    }
    .btns{
      margin-top: 20px;
    }
  }

  header.login{
    display: flex;
    align-items: center;
    background: @bgcolor;
    h1{
      margin: 0;
      padding: 0;
      color: #fff;
      font-size: 40px;
      flex: 1;
      a{
        color: #fff;
      }
    }

    .edit{
      color: #fff;
      font-size: 30px;
    }

    .avatar{
      width: 40px;
      height: 40px;
      border: 1px solid #fff;
      border-radius: 50%;
      margin-left: 15px;
    }

    .user{
      position: relative;

      ul{
        display: none;
        position: absolute;
        right: 0;
        list-style: none;
        border: 1px solid #eaeaea;
        margin: 0;
        padding: 0;
        background: #fff;
        width: 68px;
        text-align: center;
        a{
          text-decoration: none;
          color: #333;
          display: block;
          padding: 5px 10px;
          &:hover{
            background: #eaeaea;
          }
        }
      }
      &:hover ul{
        display: block;
        font-size: 14px;
      }
    }

  }
</style>



